import { Injectable } from "@angular/core";
import { HttpClient } from '@angular/common/http';
import { AppUrl} from '../app.url';

@Injectable()

export class AccountService {
  accountarr = [];
  select;
  constructor(
    private http: HttpClient
  ) {

  }


  saveAccount(e) {
    return (this.http.post(`${AppUrl.BackEndUrl}/account`, { account: e }).toPromise())

  }

  getAccount() {
    return new Promise((resolve, reject) => {
      (this.http.get(`${AppUrl.BackEndUrl}/account`).toPromise())
        .then(data => {
          //console.log(this.accountarr[0])
          if (this.accountarr[0] != undefined) { }
          else {
            for (let i = 0; i < Object.keys(data).length; i++) {
              this.accountarr.push({ id: data[i]['id'], account: data[i]['account'] })
            }
            //console.log(this.accountarr[0])
            resolve(this.accountarr[0])

          }
        })
    })
  }


}