import { Injectable } from "@angular/core";
import { AccountService } from '../account/account.service';
import { HttpClient } from '@angular/common/http';
import { DisplayService } from '../display/display.service';
import { Router } from '@angular/router';
import { AppUrl} from '../app.url';

@Injectable()

export class RunService{
    constructor(
        private accountService:AccountService,
        private http:HttpClient,
        private displayService:DisplayService,
        private router:Router
    
    ){
       
    }
    ngOnInit(){
        
    }

    testsave(a,t,d,p,id){
        (this.http.post(`${AppUrl.BackEndUrl}/run`,({account:a,type:t,detail:d,price:p,id:id})).toPromise())
        .then(()=>{
            this.displayService.getDataAccount(a).then(
                ()=>{
                  this.displayService.data = []
                  this.displayService.money = []
                  console.log(this.displayService.data)
                this.router.navigate(['/display'])
                }
              )
        })
        
    }



}